import MyDownloadsDetailPage from "@/Components/MyDownloads/MydownloadsDetail";
import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "My Downloads";

  return createMetadata({
    title: title,
  });
}

const Page = async (props) => {
  const params = await props.params;
  return <MyDownloadsDetailPage id={params?.slug} />;
};

export default Page;
